body {
  margin: 0;
  background: #ffffff;
  color: #000000;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.heading{
  background: #252525;
  color: #ffffff;
  font-family: monospace;
  letter-spacing: 1em;
  padding: 1% 1%;
  display: flex;
  flex-wrap: wrap;
}

.hrLine{
  border: none;
  outline: none;
}

.btns{
  display: flex;
  flex-wrap: wrap;
  text-align: end;
  position: absolute;
  right: 1%;
  padding: 1.5% 1%;
  word-spacing: 1em;
  width: 50%;
  text-anchor: end;
}

.link{
  color: #b6b2b2;
  text-decoration: none;
  margin-left: 3%;
  text-align: right;
}

.link:hover{
  color: #ffffff;
}

.Block{
  margin: 1% 1%;
  display: flex;
  flex-wrap: wrap;
  background: #000000b7;
  color: #ffffff;
}

.image{
  padding: 1% 1%;
  width: 25%;
}

.projecticon{
  width: 100%;
}

.projectshortdetail{
  padding: 0 1% 0 1%;
  margin-top: -1%;
}